import {ActionType, ActionCreator} from './actions';
import NameSpace from '../name-space';
import {extend} from '../../utils';

const NAME_SPACE = NameSpace.DATA;

const initialState = {
  code: null,
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case ActionType.CODE:
      return extend(state, {code: action.payload});
  }

  return state;
};

const Operation = {
  setCode: (code) => (dispatch, getState) => {
      dispatch(ActionCreator.setCode(code));

      fetch(`https://api.instagram.com/oauth/access_token`, {
          headers: {
              'Content-Type': `text/plain;charset=utf-8`
          },
          body: {
              'client_id': `1576420319199590`,
              'client_secret': `f1633dbb351aac66a73d763e06497079`,
              'grant_type': `authorization_code`,
              'redirect_uri': `https://volokhovoleg.github.io/`,
              code
          },
          method: `POST`
      })
          .then((response) => console.log(response))
          .catch((error) => {
              throw error;
          });
  }
};

export {reducer, Operation, ActionType, ActionCreator};
